<template>
  <div class="search-form">
    <div class="input-group">
      <input type="text" class="form-control main-search-control" placeholder="Sk" v-model="localSearchPhrase">
      <span class="input-group-btn main-page-search">
      <span @click="resetFilters" class="reset-filters"></span>
      <router-link v-bind:to="{name: redirectTo}">
        <button class="btn btn-default main-page-search-btn" @click="handleClick" type="submit">
            <i class="fa fa-search"></i>
        </button>
      </router-link>
    </span>
    </div>
    <small id="emailHelp" class="form-text text-muted under-search">
      Visa Namn Som
      <span class="search-criteria" @click="chooseCriteriaWindow">{{search_criteria.name}} <i class="fas fa-angle-down"></i>
        <div v-if="seen" class="criterias-list">
          <div v-for="criteria, index in criterias" @click="chooseCriteria(index)" v-if="!criteria.chosen">{{criteria.name}}</div>
        </div>
      </span>
      <span class="error-msg" v-if="errorMsg.length > 0">{{errorMsg}}</span>
    </small>
  </div>
</template>


<script>
import {mapState, mapMutations} from 'vuex'
export default {
    name: 'search-form',
    data () {
      return {
        seen: false,
        search_criteria: {},
        localSearchPhrase: "",
        currentPage: "",
        redirectTo: "search-page",
        errorMsg: "",
        criterias: [
            {name: "brjar med skstrngen ", action: "start", chosen: true},
            {name: "innehar sokstrngen", action: "middle", chosen: false},
            {name: "slutar med skstrngen", action: "end", chosen: false},
        ],
      }
    },
  methods: {
    chooseCriteriaWindow () {
        this.seen = !this.seen
    },
    chooseCriteria (index) {
      this.criterias.map(criteria => { return criteria.chosen = false });
      this.criterias[index]['chosen'] = true;
      this.$store.commit('changeCriteria', this.criterias[index]['action']);
      this.search_criteria = this.criterias[index];
    },
    handleClick () {
      this.errorMsg = "";
      let valid = this.checkInput();
      if (!valid) {
          this.errorMsg = 'incorrect value';
          return false;
      }
      this.$store.commit('changeDoSearch', true);
      this.$store.commit('test', this.localSearchPhrase);
      console.log(this.$store.state)
    },
    resetFilters() {
      this.chooseCriteria(0);
      this.localSearchPhrase = "";
      this.$store.commit('test', this.localSearchPhrase);
      this.$store.commit('changeDoSearch', true);
    },
    checkInput () {
      let validRegEx = /^[a-zA-Z() ]+$/;
      let validator = false;
      if(validRegEx.test(this.localSearchPhrase)){
          validator = true;
      }
      return validator;
//      return true
    }
  },
  computed: {
      searchObject: {
          get() {
              return this.$store.state.searchObject
          },
      }
  },
  created() {
    this.currentPage = this.$route.name;
    this.localSearchPhrase = this.$store.state.searchObject.search_phrase;
    if (this.currentPage != "start-page") {
        this.redirectTo = this.currentPage
    }
    this.criterias.map(criteria => {
        if (criteria.action == this.$store.state.searchObject.search_criteria) {
            criteria.chosen = true;
            this.search_criteria = criteria
        } else {
            criteria.chosen = false
        }
    });
  }
}
</script>

<style>
  .search-form {
    padding-left: 20px;
    padding-right: 20px;
  }
  .input-group {

    border-color: #38c8b2;
  }
  .input-group {
    position: relative;
  }
  .main-page-search {
    position: absolute;
    border-radius: 5px;
    right: 0;
    z-index: 4;
  }
  .main-page-search-btn, .main-page-search-btn:hover, .main-page-search-btn:active, .main-page-search-btn:focus {
    background: #38c8b2;
    color: #fff;
    height:52px;
    width: 53px;
    border: 2px solid #38c8b2;
    border-radius: 5px;
    box-shadow: none;
  }
  .main-search-control {
    border: 2px solid #38c8b2;
    height: 52px;
    box-shadow: 0px 2px 5px #8edcd1;
    font-family: 'Quicksand';
    border-radius: 5px;
  }
  .input-group>.custom-select:not(:last-child), .input-group>.form-control:not(:last-child){
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
  }
  .main-search-control:focus {
    background-color: #fff;
    border-color: #38c8b2;
    outline: 0;
    box-shadow: 0 0 0 0.2rem rgba(29, 255, 92, 0.25);
  }
  .under-search{
    color: #424242;
    font-family: 'Quicksand';
    font-size: 14px;
    text-align: left;

  }
  .search-criteria{
    font-family: 'Quicksand-Bold';
    cursor: pointer;
    position: relative;
  }
  .criterias-list{
    position: absolute;
    padding-left: 15px;
    padding-top: 10px;
    border:1px solid #38c8b2;
    height: 70px;
    width: 210px;
    right:-15px;
    background: #ffffff;
    z-index: 5;
    line-height: 25px;
  }
  .error-msg{
    color: #ff6f72;
  }
  .reset-filters{
    background: url("../../assets/Close.png");
    display: inline-block;
    width: 22px;
    height: 22px;
    top: 28%;
    left:-25px;
    z-index: 100;
    cursor: pointer;
    position: absolute;
  }
</style>



